<template>
  <div>
    <div class="media text-left mb-3 mt-3 py-3 mb-3 border-top border-primary">
      <img src="../assets/大頭照.jpg" class="mr-3" alt="頭像照片" width="64">
      <div class="media-body ">
        <h5 class="mt-0">Trying to record your daily life!</h5>
        <textarea name="" id="" cols="50" rows="2" placeholder="今天想說點什麼呢?"></textarea>
      </div>
    </div>
    <div class="media text-left py-3 mb-3 border-top border-primary">
      <img src="../assets/大頭照.jpg" class="mr-3" alt="頭像照片" width="64">
      <div class="media-body">
        <h5 class="mt-0">2020/10/6</h5>
        <p>複習如何製作視差滾動的效果，以及在滾動到特定位置後才將網頁上的內容作顯示。</p>
        <p>繼續完善我的部落格空間。</p>
      </div>
    </div>
    <div class="media text-left py-3 mb-3 border-top border-primary">
      <img src="../assets/大頭照.jpg" class="mr-3" alt="頭像照片" width="64">
      <div class="media-body">
        <h5 class="mt-0">2020/10/6</h5>
        <p>複習如何在Vue裡面新增bootstrap的功能，還有如何啟用bootstrap的JS功能。</p>
        <p>需要使用npm安裝jquery、popper.js，才能啟用bootstrap的JS功能。</p>
        <p>在main.js中先引進bootstrap的套件，然後在要啟用JS及jQuery功能的元件中，將$給引進來，如此一來就能將jquery的功能啟用了。</p>
        <a href="https://pjchender.github.io/2017/09/26/vue-%E5%9C%A8-vue-%E4%B8%AD%E4%BD%BF%E7%94%A8%EF%BC%88es6-import%EF%BC%89-bootstrap4-%E5%92%8C-jquery/">參考網站一</a>
        <a href="https://codertw.com/%E5%89%8D%E7%AB%AF%E9%96%8B%E7%99%BC/236973/">參考網站二</a>
      </div>
    </div>
    <div class="media text-left py-3 mb-3 border-top border-primary">
      <img src="../assets/大頭照.jpg" class="mr-3" alt="頭像照片" width="64">
      <div class="media-body">
        <h5 class="mt-0">2020/10/6</h5>
        <p>練習在Vue Cli中的元件使用props來做資料的接收，用emit將事件傳遞出去，emit還可以將參數給傳遞出去，這樣的使用手法可能用需要再多熟練一些比較好。</p>
        <p>熟悉ESLint的寫法。</p>
        <p>如果要將使用Vue寫的網站能運作，輸入指令: <strong>npm run build</strong>，預設會將網站儲存在dist的資料夾，將這個dist資料夾放到VSCode運作起來，再使用preview on webserver的工具來預覽網站是否能正常運行。<br>
          如果要放到像是Github page上面的話，到config資料夾裡面index.js的檔案去，找到build下的assetsPublicPath，將內容從'/'改成'/EthanNote/'，這樣一來才能在Github page上面正常運行。</p>
        <p>如果要加入環境變數，就到config資料夾底下的dev.env.js檔案去，在module.exports底下新增要加入的環境變數，要特別注意的是，值的部分除了單引號外裡面還要加上雙引號，例如: NODE_ENV: '"development"'。<br>
          如果要使用環境變數的值，使用process.env.name的方式。例如: process.env.NODE_ENV。</p>
        <p>使用Bootstrap，如果在類別上加上text-muted，可以將文字灰化。</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyRecord',
  data () {
    return {
    }
  }
}
</script>

